.catalog {
  display: grid;
  grid-template-columns: repeat(1, $product-card-width);
  grid-auto-flow: row;
  gap: 46px 48px;
  padding: 50px 40px;
  justify-content: center;

  @media (min-width: 488px) {
    grid-template-columns: repeat(2, $product-card-width);
  }

  @media (min-width: 768px) {
    grid-template-columns: repeat(3, $product-card-width);
  }

  @media (min-width: 1024px) {
    grid-template-columns: repeat(4, $product-card-width);
  }
}
